        .move(-4)
        .type("o")
        .move(null, { to: "END" })
        .type("h of today.")
        .pause(500)
        .break({ delay: 500 })
        .break({ delay: 500 })
        .type("<em>- Will Rogers</em>")
        .go();
    },
  },
};
