<template>
  <transition name="modal" appear>
    <div class="modal modal-overlay" @click.self="$emit('close')">
      <div class="modal-window">
        <div class="modal-content">
          <img
            class="marketing_exm"
            src="https://cdn.discordapp.com/attachments/1089496742019477526/1089498775963312148/meimage.png"
            alt=""
          />
          <img
            class="marketing_exm"
            src="https://cdn.discordapp.com/attachments/1089853488982401046/1093171130388463698/c58c0d7a0ff16232.png"
            alt=""
          />
        </div>

        <footer class="modal-footer"></footer>
      </div>
    </div>
  </transition>
</template>

<script>
/* eslint-disable */
document.querySelector("body").addEventListener("click", () => {
  const modal = document.querySelector(".modal");
  modal.classList.remove("show");
});

export default {
  name: "app",
  data() {
    return {
      modal: false,
      message: "",
    };
  },
  methods: {
    openModal() {
      this.modal = true;
    },
    closeModal() {
      this.modal = false;
    },
    doSend() {
      if (this.message.length > 0) {
        alert(this.message);
        this.message = "";
        this.closeModal();
      } else {
        alert("메시지를 입력해주세요.");
      }
    },
  },
};
</script>

<style scoped>
.marketing_exm {
  width: 1000px;
  margin: 50px;
}
.modal-content {
  height: 700px;
  margin: 100px;
}
.modal.modal-overlay {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  z-index: 30;
  top: 10%;
  left: 10%;
  width: 80%;
  height: 80%;
  background: rgba(0, 0, 0, 0.5);
  overflow-y: auto;
}

.modal.window {
  background: #fff;
  border-radius: 4px;
  overflow: hidden;
}

content.modal {
  padding: 10px 20px;
}

footer.modal {
  background: #ccc;
  padding: 10px;
  text-align: right;
}

.modal-enter-active,
.modal-leave-active {
  transition: opacity 0.4s;
}

.modal-enter-active,
.modal-leave-active.modal-window {
  transition: opacity 0.4s, transform 0.4s;
}

.modal-leave-active {
  transition: opacity 0.6s ease 0.4s;
}

.modal-enter,
.modal-leave-to {
  opacity: 0;
}

.modal-enter,
.modal-leave-to.modal-window {
  opacity: 0;
  transform: translateY(-20px);
}
</style>
